/* ##header */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: beige;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  color: #000;
}

body {
  min-height: 100vh;
  font-size: 1.5rem;
}

input,
textarea,
button,
select {
  font: inherit;
  margin-bottom: 1em;
}

header {  
  padding: 20px 0;
  text-align: center;
  font-size: 25px;
  display: flex;
  justify-content: space-between;
  background-color: black;
  color: whitesmoke;
  left: 0;
  top: 0;
  right: 0;
  z-index: 999; 
}

nav {
  display: flex;
  justify-content: flex-end;
}

nav ul {
  list-style-type: none;
}

nav ul li {
  display: inline-block;
  margin-right: 1rem;
}

nav a, nav a:visited {
  color: #fff;
  text-decoration: none;
}

nav a:hover, nav a:focus {
  text-decoration: underline;
}


/* Home.css */

.home-container {
  text-align: center;
  padding: 50px;
  background-color: #f7f7f7;
}

h1 {
  font-size: 36px;
  color: #fff;
}

p {
  font-size: 18px;
  color: #333;
  margin: 20px 0;
}

.view-all-link {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  font-size: 16px;
  border-radius: 5px;
}


