/* ##FOOTERR */


.footer {
  background-color: #000;
  color: #fff;
  padding: 20px 0;
  text-align: center;
  font-size: 18px;
  width: 100%;
  position: fixed;
  bottom: 0;
  left: 0; 
  right: 0; 
  z-index: 999;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.copyright-symbol {
  font-size: 24px;
  vertical-align: middle;
  margin-right: 5px;
}




  