.single-post-page {
  background-color: #f9f9f9;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
  margin: 20px;
}

.post-not-found {
  font-size: 24px;
  color: #333;
}

.your-post{
background-color: white;
}

h3 {
  font-size: 28px;
  color: #333;
  margin-bottom: 20px;
}

.post-content {
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 20px;
}

.post-title {
  font-size: 24px;
  color: #333;
  margin-bottom: 10px;
}

.post-text {
  font-size: 16px;
  color: #555;
  margin-bottom: 20px;
}

.post-details {
  font-size: 14px;
  color: #777;
}

.edit-button,
.delete-button {
  background-color: #007bff;
  color: #fff;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-right: 10px;
  text-decoration: none;
}

.delete-button {
  background-color: #ff0000;
}

.edit-button:hover,
.delete-button:hover {
  background-color: #0056b3;
}

.edit-button:disabled,
.delete-button:disabled {
  background-color: gray;
  cursor: not-allowed;
}


